/************************************************

Global

Place generic, global styles here. 

*************************************************/
* {
  box-sizing: border-box; }

/**
 * Main navigation styles.
 * 
 */
.main-navigation-wrapper .main-navigation {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0; }
  @media (--medium) {
    .main-navigation-wrapper .main-navigation {
      flex-direction: row; } }
  .main-navigation-wrapper .main-navigation li {
    list-style: none; }
  .main-navigation-wrapper .main-navigation a {
    margin-right: 2rem; }
  .main-navigation-wrapper .main-navigation .menu-item-has-children > a {
    position: relative;
    display: flex;
    align-items: center; }
    .main-navigation-wrapper .main-navigation .menu-item-has-children > a:after {
      content: "";
      display: inline-block;
      border-bottom: 1px solid #000;
      border-right: 1px solid #000;
      height: 0.25rem;
      width: 0.25rem;
      margin-left: 0.5rem;
      transform: rotate(45deg); }
  .main-navigation-wrapper .main-navigation .menu-item-has-children .sub-menu {
    display: none; }
  .main-navigation-wrapper .main-navigation .menu-item-has-children.open > .sub-menu {
    display: block; }
  .main-navigation-wrapper .main-navigation button {
    background: transparent;
    border: 0; }

/* WordPress has a lot of necessary default styles.*/
/* These will rarely need to be modified.*/
/**
 * Default WordPress image alignment styles.
 */
.alignleft, img.alignleft {
  margin-right: 1.5em;
  display: inline;
  float: left; }

.alignright, img.alignright {
  margin-left: 1.5em;
  display: inline;
  float: right; }

.aligncenter, img.aligncenter {
  margin-right: auto;
  margin-left: auto;
  display: block;
  clear: both; }

/**
 * Default WordPress gallery styles.
 */
.gallery {
  margin-bottom: 1.5em; }

.gallery-item {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 100%; }

.gallery-columns-2 .gallery-item {
  max-width: 50%; }

.gallery-columns-3 .gallery-item {
  max-width: 33.33%; }

.gallery-columns-4 .gallery-item {
  max-width: 25%; }

.gallery-columns-5 .gallery-item {
  max-width: 20%; }

.gallery-columns-6 .gallery-item {
  max-width: 16.66%; }

.gallery-columns-7 .gallery-item {
  max-width: 14.28%; }

.gallery-columns-8 .gallery-item {
  max-width: 12.5%; }

.gallery-columns-9 .gallery-item {
  max-width: 11.11%; }

.gallery-caption {
  display: block; }

/**
 * Default WordPress media styles.
 */
video, object {
  max-width: 100%;
  height: auto; }

pre {
  background: #eee;
  border: 1px solid #cecece;
  padding: 10px; }

.wp-caption {
  max-width: 100%;
  background: #eee;
  padding: 5px; }
  .wp-caption img {
    max-width: 100%;
    margin-bottom: 0;
    width: 100%; }
  .wp-caption p.wp-caption-text {
    font-size: 0.85em;
    margin: 4px 0 7px;
    text-align: center; }

/**
 * Screen Reader Text
 *
 * Hide text from vision that is only useful for screen readers users.
 * https://make.wordpress.org/accessibility/handbook/markup/the-css-class-screen-reader-text/
 *
 */
.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important; }

.screen-reader-text:focus {
  background-color: #eee;
  clip: auto !important;
  clip-path: none;
  color: #444;
  display: block;
  font-size: 1em;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */ }

/**
 * Default WordPress typography styles.
 */
dfn, cite, em, i {
  font-style: italic; }

blockquote {
  margin: 0 1.5em; }

address {
  margin: 0 0 1.5em; }

pre {
  background: #eee;
  font-family: "Courier 10 Pitch", Courier, monospace;
  font-size: 15px;
  font-size: 0.9375rem;
  line-height: 1.6;
  margin-bottom: 1.6em;
  max-width: 100%;
  overflow: auto;
  padding: 1.6em; }

code, kbd, tt, var {
  font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
  font-size: 15px;
  font-size: 0.9375rem; }

abbr, acronym {
  border-bottom: 1px dotted #666;
  cursor: help; }

mark, ins {
  background: #fff9c0;
  text-decoration: none; }

big {
  font-size: 125%; }
